.attribute {
    display: inline-block;
    vertical-align: text-top;
    border: solid;
    padding-left: 1%;
    padding-right: 1%;
    margin: auto;
    margin-right: 1%;
    margin-left: 1%;
    width: 600px;
    padding-bottom: 1%;
    margin-bottom: 1%;
}

.attribute-line {
    display: inline-block;
    border: solid;
    padding-left: 5px;
    padding-right: 5px;
    margin-right: 5px;
    margin-left: 5px;
}

.attribute-h4 {
    margin-right: 5px;
    margin-left: 5px;
    display: inline-block;
}

.attribute-selection {
    display: inline-block;
    padding-left: 1%;
    padding-right: 1%;
    width: 200px
}

.bank-box {
    overflow: scroll;
    margin-right: 25%;
    margin-left: 25%;
    border: solid;
    margin-bottom: 1%;
    height: 200px
}

.combat-options {
    height: 200px;
    width: 300px;
    display: inline-block;
    vertical-align: text-top;
    border: solid;
    padding-left: 1%;
    padding-right: 1%;
    margin-right: 1%;
    margin-left: 1%;
    margin-top: 1%;
    overflow: scroll;
}

.equipment-slot {
    width: 100px;
    height: 100px;
    border: solid;
    display: inline-grid;
}

.inv-box {
    vertical-align: text-top;
    height: 250px;
    width: 400px;
    display: inline-block;
    border: solid;
    padding-left: 1;
    padding-right: 1%;
    margin-left: 1%;
    margin-right: 1%;
    overflow: scroll;
    padding-left: 1%;
    padding-right: 1%;
}

.map-box {
    display: inline-grid;
    height: 75px;
    width: 75px;
    border: solid;
    /* background-color: blue; */
    color: white;
    text-align: center;
    padding-top: 15px;
}

.map-row {
    display: inline-block;
    vertical-align: text-top;
    margin-right: .1%;
    border: solid;
    padding-left: .5%;
    padding-right: .5%;
    height: 450px;
    overflow: scroll
}

.menu-box {
    margin-bottom: 1%;
    margin-top: 1%;
    margin-right: 20%;
    margin-left: 20%;
    border: solid
}

.menu-button {
    width: 150px;
    margin-bottom: 1%;
    margin-top: 1%;
}

.power-book {
    overflow: scroll;
    border: solid;
    padding-left: 1%;
    padding-right: 1%;
    height: 250px
}

.team-box {
    height: 250px;
    width: 450px;
    display: inline-block;
    vertical-align: text-top;
    border: solid;
    padding-left: 1%;
    padding-right: 1%;
    overflow: scroll
}

ul {
    list-style: none;
    padding-left: 0;
}